#preloader {
  width: 100%;
  height: 100%;
  height: 100vh;
  background-color: inherit;
  z-index: 1001;
  opacity: 1;
  visibility: visible;
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: all 1s linear;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

#preloader.done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.loader {
  width: 20em;
  height: 20em;
  z-index: 1001;
  opacity: 1;
  visibility: visible;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  display: flex;
  position: relative;
}

.loader.done {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.loader .face {
  border-style: solid;
  border-radius: 50%;
  animation: 3s linear infinite animate;
  position: absolute;
}

.loader .face:nth-child(1) {
  width: 70%;
  height: 70%;
  color: #b92f2c;
  --deg: -45deg;
  border-width: .2em .2em 0 0;
  border-color: currentColor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) currentColor;
  animation-direction: normal;
}

.loader .face:nth-child(2) {
  width: 100%;
  height: 100%;
  color: #b92f2c;
  --deg: -135deg;
  border-width: .2em 0 0 .2em;
  border-color: currentColor currentColor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation-direction: reverse;
}

.loader .face:nth-child(3) {
  width: 130%;
  height: 130%;
  color: #b92f2c;
  --deg: -45deg;
  border-width: .2em .2em 0 0;
  border-color: currentColor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) currentColor;
  animation-direction: normal;
}

.loader .face:nth-child(4) {
  width: 40%;
  height: 40%;
  color: #b92f2c;
  --deg: -135deg;
  border-width: .2em 0 0 .2em;
  border-color: currentColor currentColor rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
  animation-direction: reverse;
}

.loader .face .circle {
  width: 50%;
  height: .1em;
  transform-origin: 0;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: rotate(360deg);
}

.loader .face .circle:before {
  content: "";
  width: 1em;
  height: 1em;
  background-color: currentColor;
  border-radius: 50%;
  position: absolute;
  top: -.5em;
  right: -.5em;
  box-shadow: 0 0 2em, 0 0 4em, 0 0 6em, 0 0 8em, 0 0 10em, 0 0 0 .5em rgba(255, 255, 0, .1);
}

@keyframes animate {
  to {
    transform: rotate(1turn);
  }
}

